@import url(https://fonts.googleapis.com/css?family=Alegreya|Open+Sans+Condensed:300|Pacifico);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
ul.nav{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    text-transform: uppercase;
    font-size: 12px;
    z-index: 20;
}
ul.nav li{
  padding: 15px;
  position: relative;
  text-align: center;
}
ul.nav li:hover{
  color: rgb(0, 0, 146);
  -webkit-animation-name: menu_highlight;
          animation-name: menu_highlight;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.subMenu{
    position: absolute;
    right: 0;
    top:0;
    background-color: var(--primary-hover);
    display: none;
}
.subMenu li:hover{
    background-color: var(--primary);
    border: 1px solid gray;
}
ul.nav li:hover .subMenu{
    display: block;
}
.HeaderContainer{
    background-color: var(--header-color);
    position: relative;
    padding: 25px 15px 10px;
}

.HeaderContainer .logo{
    height: 50px;
    position: absolute;
    top: 20px;
    left: 20px;
}
#PathsContainer{
    box-sizing: border-box;
    padding: 30px 10px 10px;
    width: 100%;
    min-height: 100vh;
}

#PathsContainer h1{
    text-align: center;
}
.pathContainer{
    padding: 5px 20px;
    background-color: var(--primary);
    margin: 10px 0;
    height: 200px;
    cursor: pointer;
    display: flex;
}

.descriptionContainer{
    padding: 20px;
    width: 80%;
    overflow: hidden;
}
.pathContainer:hover{
    background-color: var(--primary-hover);
}
#PathDetailContainer{
    box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 20px 20px;
    background-color: var(--background);
    background-size: cover;
    background-position: top center;
}
#PathDetailContainer img{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    opacity: .6;
    z-index: 0;
}
#PathDetailContainer .close{
    z-index: 20;
    cursor: pointer;
}
.close:hover{
    color: white;
}
.loginContainer{
    background-position: top left;
    background-image: url(https://cdn.shopify.com/s/files/1/1726/6115/articles/yoga_inner_peace_2048x.jpg?v=1515268582);
    background-size: cover;
    height: 100vh;
    width: 100%;
    position: absolute;
}

.smallLoginModal{
    display: flex;
    flex-direction: column;
    padding: 30px 50px;
    margin: 30vh auto;
    width: 90%;
    max-width: 500px;
    background-color: var(--primary-hover)
}
.smallLoginModal img{
    width: 40px;
}

@media only screen and (max-width: 750px) {
    .loginContainer{
        background-size: contain;
    }
}
#leftMenuContainer{
    padding: 20px 50px 20px 0;
    position: absolute;
    left: -250px;
    top: 5vh;
    width:90vw;
    max-width: 250px;
    background-color: gray;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    /* min-height: 80vh; */
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    -webkit-animation-name: leftMenuClose;
            animation-name: leftMenuClose;
    -webkit-animation-duration: .3s;
            animation-duration: .3s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}
#leftMenuContainer span{
    position: absolute;
    right: 5px;
    top: 50%;
}
#leftMenuContainer:hover{
    left: 0;
    -webkit-animation-name: leftMenu;
            animation-name: leftMenu;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}
#leftMenuContainer div{
    padding: 18px 50px;
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
}
#leftMenuContainer div:hover{
background-color: white;
}


 .FooterContainer{
  color: var(--text-primary);
  position: relative;
  padding: 25px 15px 10px;
}
.HomeContainer{
    color: var(--text-secondary);
    box-sizing: border-box;
    background-color: var(--background);
    min-height: 100vh;
    max-width: 100vw;
}
.HomeContainer .banner{
  position: relative;
}
.HomeContainer .banner h1, .HomeContainer .banner h5{
  position: relative;
  top: 50%;
  text-shadow: 1px 1px 2px black, 0 0 25px gray, 0 0 5px rgb(58, 58, 58);
}
.HomeContainer .aboutme, .HomeContainer .subscribe, .HomeContainer .testimonial{
  padding: 10vw 10vw;
  margin: 0 10px;
  position: relative;
  bottom: 40px;
}
.HomeContainer .banner{
  padding: 20px;
  height: 90vh;
  background-image: url(/static/media/katie-web-banner1.4e4d5de8.jpg);
  background-size: cover;
  background-position: 85% 35%;
  background-repeat: no-repeat;
}
.HomeContainer .aboutme{
  background-image: url(/static/media/katie_couple2.e681a54e.jpg);
  background-size: cover;
  background-position: 0% 50%;
  background-attachment: fixed;
}
.HomeContainer p{
    background-color: rgba(5, 5, 5, .5);
    padding: 9px;
    text-shadow: 1px 1px 2px black, 0 0 25px gray, 0 0 5px rgb(56, 56, 56);
}
.HomeContainer .subscribe{
  font-size: 19px;
  background-image: url(/static/media/katie_couple.8d7bee41.jpg);
  background-position: 30% 15%;
  background-size: cover;
  background-attachment: fixed;
  height: 50vh;
  padding-top: 20vh;
}
.HomeContainer .testimonial{
  color: var(--text-primary);

}
.HomeContainer .cardContainer{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
.HomeContainer .cardContainer .card .pic{
  border-radius: 50%;
  box-shadow: 4px 4px 50px black;
  height: 100px;
  width: 100px;
  background-size: 130%;
  background-position: 50% 50%;
}
.testimonial .name, .testimonial .title, .testimonial .pic{
  margin: 15px auto 25px;
}
.testimonial .card .pic.one{
  background-image: url(/static/media/review1.9c4d7ec7.png);
}
.testimonial .card .pic.two{
  background-image: url(/static/media/review2.e666e0cd.png);
}
.testimonial .card .pic.three{
  background-image: url(/static/media/review3.46be0779.png);
}
.HomeContainer .cardContainer .card {
  padding: 30px;
  text-align: center;
}

html, body, li, ul , div, span {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
  cursor: default;
}

/************          Variables         ***************/


:root {
  --c-green: #6F934C;
  --c-yellow: #E5B642;
  --background: var(--primary);
  --black: #020402;
  --red: #AF3236;

  --text-primary: #020402;
  --text-secondary: white;
  --text-primary-hover: rgb(90, 90, 90);
  --text-secondary-hover: #b9b9b9;

  --primary: whitesmoke;
  --secondary: rgb(109, 11, 11);
  --primary-hover: rgba(192, 192, 192, 0.8);
  --secondary-hover: rgb(100, 41, 41);

  --header-color: rgb(163, 163, 163);

}

/************          Global Styling         ***************/

html, body{
  color: #020402;
  color: var(--text-primary);
}
ul{
  display: inline;
}
input, textarea{
  font-size: 1em;
  outline: none;
  padding: 3px 0px 3px 3px;
  margin: 5px 1px 3px 0px;
  border: 1px solid #DDDDDD;
}
input:focus, textarea:focus{
  box-shadow: 0 0 5px rgba(81, 203, 238, 1);
  padding: 3px 0px 3px 3px;
  margin: 5px 1px 3px 0px;
  border: 1px solid rgba(81, 203, 238, 1);
}
.hidden{
  display: none;
}
a, li{
  list-style-type: none;
  text-decoration: none;
}
:visited{
  color: #020402;
  color: var(--text-primary);
}
button:hover, li:hover, input[type='submit']{
  cursor: pointer;
}
h1, h2, h3, h4, h5, h6, h7 {
  text-align: center;
}
p{
  margin: 0;
}
/************          Styling for custom components         ***************/




button1{
  color: var(--c-white);
  background-color: rgb(109, 11, 11);
  background-color: var(--secondary);
  border-radius: 25px;
  padding: .5em 2em;
  border: none;
}

button1:hover{
  background-color: rgb(100, 41, 41);
  background-color: var(--secondary-hover);
  box-shadow:0 3px 11px -2px black;
}
/* no styling for printing*/
@-webkit-keyframes leftMenu {
  from {left: -250px;}
  to {left: 0;}
}
@keyframes leftMenu {
  from {left: -250px;}
  to {left: 0;}
}
@-webkit-keyframes leftMenuClose {
  from {left: 0px;}
  to {left: -250px;}
}
@keyframes leftMenuClose {
  from {left: 0px;}
  to {left: -250px;}
}
@-webkit-keyframes navMenu {
  from {top: --header-height);top: var(-(--header-height));}
  to {top: 0;}
}
@keyframes navMenu {
  from {top: --header-height);top: var(-(--header-height));}
  to {top: 0;}
}
@-webkit-keyframes navMenuClose {
  from {top: 0px;}
  to {top: -250px;}
}
@keyframes navMenuClose {
  from {top: 0px;}
  to {top: -250px;}
}
@keyFrames menu_highlight {
  from {background-color: var(--background);}
  to {background-color: var(--primary-hover);}
}
